/* ----- Flex ----- */
.no-shrink {
    flex-shrink: 0;
}

.flex-grow {
    flex-grow: 1;
}

.justify-center {
    justify-content: center;
}

.flex-wrap {
    flex-wrap: wrap;
}

.no-wrap {
    flex-wrap: nowrap !important;
}

.ignore-responsive-center {
    width: fit-content !important;
    margin-left: auto;
    margin-right: auto;
}

.align-center {
    align-items: center;
}

.no-gap {
    gap: 0px;
}

.small-gap {
    gap: 3px;
}

.overflow-y-scroll {
    overflow-y: scroll;
}

/* ----- Sizing ----- */
.fit-content {
    width: fit-content;
}

.no-min {
    min-height: 0px;
    min-width: 0px;
}

.full-page {
    width: 100vw;
    height: 100vh;
}

.width-100 {
    width: 100%;
}

/* ----- Style ----- */
.round {
    border-radius: 100px;
}

.focus {
    background-color: var(--dawn-control-focus) !important;
}

.underline {
    text-decoration: underline;
}

/* ----- UX ----- */
.no-select {
    -webkit-touch-callout: none;
    -webkit-user-select: none;
    -khtml-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
}

.hover-grow {
    transition: 0.2s;
}

.hover-grow:hover {
    transform: scale(1.25);
}

.hover:hover {
    background-color: var(--dawn-control-hover);
    cursor: pointer;
}

.clickable:hover {
    cursor: pointer;
}
