body {
  /* ----- Baes Colors ----- */
  --dawn-neutral-base-color: 250;
  --dawn-danger-base-color: 0;
  --dawn-success-base-color: 90;
  --dawn-accent-base-color: 263;

  /* ----- Background Colors ----- */
  --dawn-root-base: hsla(var(--dawn-accent-base-color), 20%, 10%, 1);
  --dawn-root: #fff;

  --dawn-container-background: hsla(var(--dawn-neutral-base-color), 5%, 20%, 1);
  --dawn-container-hover: hsla(var(--dawn-neutral-base-color), 5%, 25%, 1);
  --dawn-container-focus: hsla(var(--dawn-neutral-base-color), 5%, 30%, 1);
  --dawn-container-border: hsla(var(--dawn-neutral-base-color), 5%, 25%, 1);

  --dawn-control-background: hsla(var(--dawn-neutral-base-color), 5%, 30%, 1);
  --dawn-control-hover: hsla(var(--dawn-neutral-base-color), 5%, 25%, 1);
  --dawn-control-focus: hsla(var(--dawn-neutral-base-color), 5%, 40%, 1);
  --dawn-control-border: hsla(var(--dawn-neutral-base-color), 5%, 25%, 1);
  --dawn-control-disabled: hsla(var(--dawn-neutral-base-color), 5%, 15%, 1);

  --dawn-container-secondary-background: hsla(
    var(--dawn-neutral-base-color),
    5%,
    20%,
    1
  );

  --dawn-popup-background: hsla(var(--dawn-neutral-base-color), 60%, 5%, 1);

  /* ----- Danger Colors ----- */
  --dawn-danger-base: hsla(var(--dawn-danger-base-color), 75%, 30%, 1);
  --dawn-danger-base-text: white;
  --dawn-danger-base-hover: hsla(var(--dawn-danger-base-color), 90%, 40%, 1);
  --dawn-danger-text: hsla(var(--dawn-danger-base-color), 100%, 50%, 1);

  /* ----- Sucess Colors ----- */
  --dawn-success-base: hsla(var(--dawn-success-base-color), 75%, 30%, 1);
  --dawn-success-base-text: white;
  --dawn-success-base-hover: hsla(var(--dawn-success-base-color), 90%, 40%, 1);
  --dawn-success-text: hsla(var(--dawn-success-base-color), 100%, 50%, 1);

  /* ----- Accent Colors ----- */
  --dawn-accent-base: hsla(var(--dawn-accent-base-color), 45%, 80%, 1);
  --dawn-accent-base-text: black;
  --dawn-accent-base-hover: hsla(var(--dawn-accent-base-color), 45%, 85%, 1);
  --dawn-accent-text: hsla(var(--dawn-accent-base-color), 45%, 80%, 1);

  /* ---- Special Colors ----- */
  --dawn-extra-shadow: hsla(var(--dawn-accent-base-color), 80%, 20%, 1);

  /* ----- Values ----- */
  --dawn-hover-transition: 0.2s;
  --dawn-border-radius: 20px;
  --dawn-control-border-radius: 5px;
  --dawn-flex-gap: 20px;
  --dawn-page-width: 60%;
}

/* ----- Apply default styles for elements ----- */
:is(.dawn-normal-bg, button, input) {
  background-color: var(--dawn-control-background);
}

:is(.dawn-normal-bg, button, input):hover {
  background-color: var(--dawn-control-hover);
}

:is(.dawn-normal-bg, input):focus {
  background-color: var(--dawn-control-focus);
}

.dawn-danger-bg {
  background-color: var(--dawn-danger-base);
  color: var(--dawn-danger-base-text);
}

.dawn-danger-bg:hover {
  background-color: var(--dawn-danger-base-hover);
}

.dawn-danger-fg {
  color: var(--dawn-danger-text);
}

.dawn-success-bg {
  background-color: var(--dawn-success-base);
  color: var(--dawn-success-base-texxt);
}

.dawn-success-bg:hover {
  background-color: var(--dawn-success-base-hover);
}

.dawn-success-fg {
  background-color: var(--dawn-success-text);
}

.dawn-accent-bg {
  background-color: var(--dawn-accent-base);
  color: var(--dawn-accent-base-text);
}

.dawn-accent-fg {
  color: var(--dawn-accent-text);
}

.dawn-accent-bg:hover {
  background-color: var(--dawn-accent-base-hover);
}

.dawn-inherit-bg {
  background-color: transparent;
  color: var(--dawn-root);
}

progress {
  border: none;
}

body {
  background-size: cover;
  background-repeat: no-repeat;
  background-attachment: fixed;

  font-family: Arial, Helvetica, sans-serif;

  margin: 0;

  background-color: var(--dawn-root-base);
  color: var(--dawn-root);
}

hr {
  width: 90%;
  border-style: solid;
  border-color: var(--dawn-control-border);
}

label {
  word-wrap: break-word;
}

a {
  color: var(--dawn-accent-base);
}
